import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { authSlice } from "./authSlice";

export const api = createApi({
  reducerPath: "api",
  baseQuery: fetchBaseQuery({ baseUrl: "http://localhost:5000/api/v1" }),
  endpoints: (builder) => ({
    getUser: builder.query({
      query: (userId) => ({
        url: `/users/find/${userId}`,
        method: "GET",
      }),
      onQueryStarted: async (
        userId,
        { dispatch, getState, queryFulfilled }
      ) => {
        console.log("Calling getUser with userId:", userId);
        try {
          // Wait for the query to be fulfilled
          const { data } = await queryFulfilled;
          console.log("User data fetched:", data);
          // Optionally update the state with fetched user data
          dispatch(
            authSlice.actions.signinSuccess({
              user: data,
              accessToken: (getState() as any).auth.accessToken,
            })
          );
        } catch (err) {
          console.error("Error fetching user data:", err);
        }
      },
    }),
    signup: builder.mutation({
      query: (data) => ({
        url: "/users/signup",
        method: "POST",
        body: data,
      }),
    }),
    signin: builder.mutation({
      query: (data) => ({
        url: "/users/signin",
        method: "POST",
        body: data,
      }),
    }),
  }),
});

export const { useSignupMutation, useSigninMutation } = api;
